<template>
  <modal class="habit-archive-info">
    <template #title>{{ t("habitInfoModal.title") }}</template>
    <template #body>
      <i18n-t tag="p" scope="global" keypath="habitInfoModal.description">
        <template v-slot:description>
          <strong>{{ habit.description }}</strong>
        </template>
      </i18n-t>
      <div v-if="habit.count" class="habit-archive-info__count">
        <svg id="habitInfoCount" width="200" height="150"></svg>
        <div class="habit-archive-info__detail">
          <div class="habit-archive-info__detail-item">
            <div
              class="habit-archive-info__color"
              :style="{ backgroundColor: colorScheme.done }"
            ></div>
            {{ t("habitInfoModal.labels.completed") }}: {{ habit.count.done }}
          </div>
          <div class="habit-archive-info__detail-item">
            <div
              class="habit-archive-info__color"
              :style="{ backgroundColor: colorScheme.skipped }"
            ></div>
            {{ t("habitInfoModal.labels.skipped") }}: {{ habit.count.skipped }}
          </div>
          <div class="habit-archive-info__detail-item">
            <div
              class="habit-archive-info__color"
              :style="{ backgroundColor: colorScheme.missed }"
            ></div>
            {{ t("habitInfoModal.labels.missed") }}: {{ habit.count.missed }}
          </div>
        </div>
      </div>
      <p v-else>You do not have any saved information.</p>
    </template>
    <template #footer>
      <button-group>
        <app-button
          @click="$emit('close')"
          variant="secondary"
        >
          {{ t("habitInfoModal.close") }}
        </app-button>
      </button-group>
    </template>
  </modal>
</template>

<script lang="ts">
import {
  defineComponent, onMounted, PropType, ref,
} from 'vue';
import * as d3 from 'd3';
import { useI18n } from 'vue-i18n';
import { HabitDetail } from '@/types/shared';
import Modal from '@/components/Modal.vue';
import AppButton from '@/components/buttons/AppButton.vue';
import ButtonGroup from '@/components/ButtonGroup.vue';

export default defineComponent({
  components: {
    Modal,
    AppButton,
    ButtonGroup,
  },
  props: {
    habit: {
      type: Object as PropType<HabitDetail>,
      required: true,
    },
  },
  setup(props) {
    const { t } = useI18n();
    const colorScheme = ref<Record<string, unknown>>({});

    onMounted(() => {
      if (!props.habit.count) return;

      const svg = d3.select('svg#habitInfoCount');
      const width = Number(svg.attr('width'));
      const height = Number(svg.attr('height'));
      const radius = Math.min(width, height) / 2;
      const g = svg
        .append('g')
        .attr('transform', `translate(${width / 2},${height / 2})`);
      const keys = Object.keys(props.habit.count);
      const values = Object.values(props.habit.count).map((value) => Number(value));
      const ordScale = d3
        .scaleOrdinal()
        .domain(keys)
        // skipped, done, missed
        .range(['#93c5fd', '#86efac', '#d1d5db']);
      const pie = d3.pie();
      const data = pie(values);
      colorScheme.value = {
        skipped: ordScale('skipped'),
        done: ordScale('done'),
        missed: ordScale('missed'),
      };

      // Generate the arcs
      const arc = d3.arc().innerRadius(0).outerRadius(radius);

      // Generate groups
      const arcs = g
        .selectAll('arcs')
        .data(data)
        .enter()
        .append('g')
        .attr('class', 'arc');

      arcs
        .append('path')
        .attr('d', arc as unknown as string)
        .attr('fill', (d: any) => ordScale(`${d.index}`) as string);
    });

    return {
      t,
      colorScheme,
    };
  },
});
</script>

<style lang="scss">
@import "@/assets/styles/utils/_functions.scss";

.habit-archive-info {
  &__count {
    @apply flex justify-around my-4;
  }

  &__detail {
    @apply justify-end;
  }

  &__detail-item {
    @apply text-sm;
  }

  &__color {
    @apply inline-block mr-1 align-middle;
    width: remSize(14px);
    height: remSize(14px);
  }
}
</style>
