<template>
  <round-item-selection>
    <round-item
      v-for="customDay in customDays"
      :key="customDay.key"
      :bgColor="customDay.bgColor"
      :color="customDay.color"
    >
      {{ customDay.translation }}
    </round-item>
  </round-item-selection>
</template>

<script lang="ts">
import { defineComponent, PropType, reactive } from 'vue';
import { useI18n } from 'vue-i18n';
import { Day, dayMap, HabitCount } from '@/types/shared';
import RoundItem from '@/components/RoundItem.vue';
import RoundItemSelection from '@/components/RoundItemSelection.vue';

export default defineComponent({
  components: {
    RoundItem,
    RoundItemSelection,
  },
  props: {
    habitDays: {
      type: Array as PropType<Day[]>,
      required: true,
    },
    habitCount: {
      type: Array as PropType<HabitCount[]>,
      default: () => ([]),
    },
  },
  setup(props) {
    const { t } = useI18n();
    const customDaysDefault = [...Array(7).keys()].map((key: number) => {
      const hasDay = Object.values(props.habitDays).includes(dayMap[key]);
      return {
        key: dayMap[key],
        translation: t(`days.abbr.${dayMap[key]}`),
        bgColor: (() => '#f3f4f6')(),
        color: hasDay ? '#000' : '#fff',
      };
    });
    const customDays = reactive(customDaysDefault);

    return {
      customDays,
    };
  },
});
</script>

