<template>
  <div class="archive-habit">
    <page-title>
      {{t('archiveHabitPage.title')}}
      <template #sub>{{t('archiveHabitPage.pageDescription')}}</template>
    </page-title>
    <habit-list v-if="archives && archives.habit.length"
      :isArchive="true"
      :habits="archives.habit"
      @info="showInfoModal"
      @delete="onShowDeleteWarning"
    />
    <card v-else flat class="habits__empty">
      <template #body>
        <alert v-if="error" variant="error" :closeable="false">{{t('habitsPage.error')}}</alert>
        <p v-else class="text-lg">{{t('habitsPage.empty')}}</p>
      </template>
    </card>

    <modal v-if="showDeleteWarning" @close="showDeleteWarning = false">
      <template #title>{{t('archiveHabitPage.deleteWarningModal.title')}}</template>
      <template #body>
        {{t('archiveHabitPage.deleteWarningModal.description')}}
      </template>
      <template #footer>
        <button-group>
          <app-button
            @click="deleteHabit"
            variant="primary"
          >
            {{t('archiveHabitPage.deleteWarningModal.btn')}}
          </app-button>
        </button-group>
      </template>
    </modal>
    <habit-archive-info
      v-if="showInfo"
      @close="showInfo = false"
      :habit="habitItem"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
import PageTitle from '@/components/PageTitle.vue';
import Card from '@/components/Card.vue';
import Alert from '@/components/Alert.vue';
import {
  Archive, AppError, ErrorCode, HabitDetail, Habit, ArchiveType,
} from '@/types/shared';
import HabitList from '@/components/habit/HabitList.vue';
import HabitArchiveInfo from '@/components/habit/HabitArchiveInfo.vue';
import Modal from '@/components/Modal.vue';
import AppButton from '@/components/buttons/AppButton.vue';
import ButtonGroup from '@/components/ButtonGroup.vue';

export default defineComponent({
  components: {
    PageTitle,
    Card,
    Alert,
    HabitList,
    Modal,
    AppButton,
    ButtonGroup,
    HabitArchiveInfo,
  },
  setup() {
    const { t } = useI18n();
    const store = useStore();
    const archives = ref<Archive>();
    const error = ref<AppError | null>(null);
    const habitItem = ref<HabitDetail>();
    const showInfo = ref(false);
    const showDeleteWarning = ref(false);

    watch(() => store.state.archives.archive, () => {
      archives.value = store.state.archives.archive;
    });

    store.dispatch('archives/fetch').then((response: unknown) => {
      if (response instanceof AppError && !ErrorCode.NotFound) {
        error.value = response;
      }
    });

    const showInfoModal = (id: string | number) => {
      habitItem.value = archives.value?.habit.find((item) => item.id === id);
      showInfo.value = true;
    };

    const onShowDeleteWarning = (habit: Habit) => {
      habitItem.value = habit;
      showDeleteWarning.value = true;
    };

    const deleteHabit = async () => {
      const result = await store.dispatch('archives/delete',
        { id: habitItem.value?.id, type: ArchiveType.Habit });

      if (result instanceof AppError) {
        console.log(result);
        return;
      }

      showDeleteWarning.value = false;
    };

    return {
      t,
      showInfo,
      deleteHabit,
      showInfoModal,
      onShowDeleteWarning,
      error,
      habitItem,
      archives,
      showDeleteWarning,
    };
  },
});
</script>

<style lang="scss">
@import '@/assets/styles/utils/_mixins.scss';

.archive-habit {
  .habits-list {
    @apply mt-6;
    @include miniContentSize;
  }
}
</style>
