
import { defineComponent, ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import { cloneDeep } from 'lodash';
import PageTitle from '@/components/PageTitle.vue';
import AddItem from '@/components/AddItem/AddItem.vue';
import AppButton from '@/components/buttons/AppButton.vue';
import ButtonGroup from '@/components/ButtonGroup.vue';
import {
  AppError, ErrorCode, Habit, ToDoItemForm,
} from '@/types/shared';
import Modal from '@/components/Modal.vue';
import Alert from '@/components/Alert.vue';
import Card from '@/components/Card.vue';
import HabitList from '@/components/habit/HabitList.vue';
import { createHabitItemReq } from '@/components/AddItem/features/helpers';
import { useAddItemState } from '@/components/AddItem/features/hooks/addItemGlobalState';
import { getLocalDate } from '@/services/utils/time';

export default defineComponent({
  components: {
    PageTitle,
    AddItem,
    AppButton,
    Modal,
    ButtonGroup,
    Alert,
    Card,
    HabitList,
  },
  setup() {
    const { setServerError, setLoading, resetAddItemModalValue } = useAddItemState();
    const { t } = useI18n();
    const route = useRoute();
    const store = useStore();
    const showAddItem = ref(!!route.query.add);
    const showDeleteWarning = ref(false);
    const selectedHabit = ref<Habit | null>();
    const habits = ref<Habit[]>(store.state.habits.habits);
    const error = ref<AppError | null>(null);
    const weekOf = getLocalDate(Date.now());

    watch(() => store.state.habits.habits, (newValue) => { habits.value = newValue; });

    const onAddNewHabit = () => {
      selectedHabit.value = null;
      showAddItem.value = true;
      resetAddItemModalValue();
    };

    const edit = async (habit: Habit) => {
      resetAddItemModalValue();
      showAddItem.value = true;
      selectedHabit.value = habit;
    };

    const fetch = () => {
      store.dispatch('habits/fetch').then((result) => {
        if (result instanceof AppError && !ErrorCode.NotFound) {
          error.value = result;
        }
      });
    };

    const onShowDeleteWarning = (habit: Habit) => {
      showDeleteWarning.value = true;
      selectedHabit.value = habit;
    };

    const refreshList = () => {
      store.dispatch('habits/createDailyList');
      fetch();
    };

    const onDeleteHabit = async () => {
      const result = await store.dispatch('habits/delete', selectedHabit.value?.id);
      showDeleteWarning.value = false;
      if (result instanceof AppError && !ErrorCode.NotFound) {
        error.value = result;
        return;
      }
      refreshList();
    };

    const updateItem = async (formData: ToDoItemForm) => {
      const result = await store.dispatch('habits/update', createHabitItemReq(cloneDeep(formData)));
      if (result instanceof AppError) {
        setLoading(false);
        setServerError(t('addItemComponent.errors.cannotUpdateItem'));
        return;
      }
      showAddItem.value = false;
      refreshList();
    };

    const addItem = async (formData: ToDoItemForm) => {
      const result = await store.dispatch('habits/add', createHabitItemReq(cloneDeep(formData)));
      if (result instanceof AppError) {
        setLoading(false);
        setServerError(t('addItemComponent.errors.cannotAddItem'));
        return;
      }
      showAddItem.value = false;
      resetAddItemModalValue();
      refreshList();
    };

    fetch();

    return {
      t,
      error,
      showAddItem,
      resetAddItemModalValue,
      addItem,
      updateItem,
      onShowDeleteWarning,
      onDeleteHabit,
      selectedHabit,
      showDeleteWarning,
      edit,
      onAddNewHabit,
      habits,
      weekOf,
    };
  },
});
