<template>
  <list class="habits-list">
    <list-item v-for="habit in habits" :key="habit.id">
      <list-content>
        <template #middle>
          <img
            v-if="habit.isPrioritized"
            src="@/assets/images/icons/star.svg"
            alt="open"
            class="daily-habit-item__prioritized"
          /> {{habit.description}}
          <habit-week :habitDays="habit.days" />
        </template>
        <template #right>
          <tool-tip v-if="!isArchive">
            <template #element>
              <icon-button :flat="true"
                @click="goToAnalytics(habit.id)"
              >
                <img src="@/assets/images/icons/analytics.svg" alt="analytics" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.analytics')}}</template>
          </tool-tip>
          <tool-tip v-if="!isArchive">
            <template #element>
              <icon-button :flat="true" @click="$emit('edit', habit)">
                <img src="@/assets/images/icons/edit.svg" alt="edit" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.edit')}}</template>
          </tool-tip>
          <tool-tip v-if="isArchive" :right="true">
            <template #element>
              <icon-button :flat="true"
                @click="$emit('info',habit.id)"
              >
                <img src="@/assets/images/icons/info.svg" alt="info" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.info')}}</template>
          </tool-tip>
          <tool-tip :right="true">
            <template #element>
              <icon-button :flat="true"
                @click="$emit('delete',habit)"
              >
                <img src="@/assets/images/icons/delete.svg" alt="delete" />
              </icon-button>
            </template>
            <template #text>{{t('habitsPage.itemBtn.delete')}}</template>
          </tool-tip>
        </template>
      </list-content>
    </list-item>
  </list>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRouter } from 'vue-router';
import List from '@/components/list/List.vue';
import ListItem from '@/components/list/ListItem.vue';
import ListContent from '@/components/list/ListContent.vue';
import IconButton from '@/components/IconButton.vue';
import ToolTip from '@/components/ToolTip.vue';
import HabitWeek from '@/components/habit/HabitWeek.vue';
import { Habit } from '@/types/shared';
import { habitDetail } from '@/services/utils/link';

export default defineComponent({
  components: {
    List,
    ListItem,
    ListContent,
    IconButton,
    ToolTip,
    HabitWeek,
  },
  props: {
    habits: {
      type: Object as PropType<Habit[]>,
      required: true,
    },
    isArchive: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const { t } = useI18n();
    const router = useRouter();

    const goToAnalytics = (id: string | number) => {
      router.push({
        name: habitDetail.name,
        params: {
          id,
        },
      });
    };

    return {
      t,
      goToAnalytics,
    };
  },
});
</script>
